//vuex
import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

export default new Vuex.Store({
	state:{
		//statedata
		//this.$store.state.
		//this.$store.state.index=index
		//mutations
		name:'', //
		index:0,	//id
		address:'' //
	},
	//mutationsmethod
	//this.$store.commit('')
	mutations:{
		name_fn(state,name){  
			state.name=name;
		},
		index_fn(state,index){
			state.index=index
		},
		addr_fn(state,addr){
			state.address=addr;
		}
	}
	// this.$store.getters.
	//getters:{
	//  getNum(state){
	//    return ''+state.name
	//  }
	//}
	

})









